import React from 'react';

import {Bar} from 'react-chartjs-2';
import {getDataLocalidades, getDataEstados, getDataEdades} from '../Opendata/data.js';
import Loader from './Loader'
import store from '../../Redux/store'

export default class BarChart extends React.Component{

    constructor(props){
        super(props);
        this.state={
            dataIsLoaded: false,
            data: [],
            Months:['January', 'February', 'March', 'April',
            'May', 'June', 'July', 'August', 'September',
            'October', 'November', 'December'],
            chartData:{
                title: 'Distribución de casos por ',
                labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
                datasets: [{
                    label: 'Dataset 1',
                    borderWidth: 1,
                    data: [ 0,0,0,0,0,0,0 ],
                    borderColor: 'rbga(120,120,120,1)'
                }]
            }
        };
        //this.dataLoader = this.dataLoader.bind(this)
    }

    getData = async ()=>{
        
        let r = Math.floor(Math.random() * 255)
        let g = Math.floor(Math.random() * 255)
        let b = Math.floor(Math.random() * 255)
        //datajson = datajson['Localidades'] 
        var  datajson = [];
        let char_data = this.state.chartData
        if (this.props.type === 'localidad'){
             datajson = await getDataLocalidades() 
            char_data.labels = datajson.localidades;
            char_data.datasets.map( dataset => {
                dataset.data = datajson.numcasosloc;
                dataset.backgroundColor = `rgba(${r},${g},${b},0.6)`
                return null
            })
        }else if (this.props.type === 'edad'){
            datajson = await getDataEdades() 
            char_data.labels= datajson.rangos;
            char_data.datasets.map( dataset => {
                dataset.data = datajson.edades;
                dataset.backgroundColor = `rgba(${r},${g},${b},0.6)`
                return null
            })
        }else{
            datajson = await getDataEstados() 
            char_data.labels= datajson.rangos;
            char_data.datasets.map( dataset => {
                dataset.data = datajson.edades;
                dataset.backgroundColor = `rgba(${r},${g},${b},0.6)`
                return null
            })
        }
        
        this.setState({chartData: char_data})
        this.setState({dataIsLoaded: true})
    }

    componentDidMount(){ this.getData() }

    render() {
        return (
        <div style={{height:'250px',paddingTop: '20px'}}>
            <h5>{this.state.chartData.title + this.props.type}</h5>
            { 
                !this.state.dataIsLoaded ? <Loader/> 
                : <Bar
                    data={this.state.chartData}
                    width={100}
                    height={50}
                    options={{ maintainAspectRatio: false }}
                />
            }
        </div>
        );
    }
};