import {
    getPositions,getDataLocalidades, 
    getDataGeneros,
    getDataEdades,
    getDataEstados
} 
from '../../Components/Opendata/data'; 


export const change_data = (option) =>{
    return {
        type: 'CHANGE_DATA',
        payload: option
    }
}

// trigger all functions
export const use_all = () =>{
    return async (dispatch) => {
        dispatch(get_casos())
        //dispatch(get_poligons())
        dispatch(get_state())
    }
    //get_casos()
}

