
import React,{ Component} from 'react';

import {connect} from 'react-redux'


import PieChart from './PieChart.js';
import BarChart from './BarChart.js';
import LinesChart from './LinesChart.js';
import {casesState} from '../Opendata/staticData.js';

//import LinePieChart from './LineChart.js';

class Graphic extends Component{

    constructor(props){
        super(props);

        this.state={ };
        
    }
    
    render(){
        
        return(
            <div className="row" style={{height:'250px',paddingTop: '20px'}}>
                <div className="col-md-6"> <BarChart type='edad'/></div>
                <div className="col-md-6"> <PieChart/> </div>
                <div style={{paddingTop: '40px'}}/>
                <div  className="col-md-12"> <BarChart type='localidad'/></div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       User: state.reducer_user
    };
};

export default connect(mapStateToProps,null)(Graphic);