  // casos por genero en localidades
const getCasosPorGenero =(data)=> {
    let dic_loc = {
        "Engativá": [0,0], "Usaquén" : [0,0],
        "Fontibón": [0,0], "Kennedy": [0,0],
        "Suba" : [0,0], "Chapinero": [0,0],
        "Ciudad Bolívar": [0,0], "Barrios Unidos" : [0,0],
        "La Candelaria": [0,0], "Rafael Uribe Uribe": [0,0],
        "Puente Aranda" : [0,0], "Usme" : [0,0],
        "Teusaquillo" : [0,0], "Tunjuelito" : [0,0],
        "Bosa" : [0,0], "Fuera de Bogotá" : [0,0],
        "Santa Fe" : [0,0], "Antonio Nariño" : [0,0],
        "San Cristóbal" : [0,0],"Los Mártires" : [0,0],
        "Sin Dato" : [0,0],
    }

    data.map( (per) => {
        var localidad = per["Localidad de residencia"];
        if(per["Sexo"] === "F") dic_loc[localidad][0]+=1
        else dic_loc[localidad][1]+=1
        return null
    })

    let generos = []
    const keys = Object.keys(dic_loc);
    for(let i=0; i < 2; i++){
        let a = 0
        keys.map( (e) =>{ a += dic_loc[e][i]; return null})
        generos.push(a)
    }

    let tot = parseInt(generos[0])+parseInt(generos[1])
    generos[0] = (generos[0]/tot)*100
    generos[1] = (generos[1]/tot)*100
    return generos
}

 // casos por genero en localidades
 const getCasosPorEdad =(data)=> {
    let loc_edades = {
        /* Clases de edades por localidad: (0-9), (10-19), (20-29), (30-39), (40-49), (50-69), (60, 69), (70-79), 80+ */
        "Engativá":           [0,0,0,0,0,0,0,0,0],
        "Usaquén" :           [0,0,0,0,0,0,0,0,0],
        "Fontibón":           [0,0,0,0,0,0,0,0,0],
        "Kennedy":            [0,0,0,0,0,0,0,0,0],
        "Suba" :              [0,0,0,0,0,0,0,0,0],
        "Chapinero":          [0,0,0,0,0,0,0,0,0],
        "Ciudad Bolívar":     [0,0,0,0,0,0,0,0,0],
        "Barrios Unidos" :    [0,0,0,0,0,0,0,0,0],
        "La Candelaria":      [0,0,0,0,0,0,0,0,0],
        "Rafael Uribe Uribe": [0,0,0,0,0,0,0,0,0],
        "Puente Aranda" :     [0,0,0,0,0,0,0,0,0],
        "Usme" :              [0,0,0,0,0,0,0,0,0],
        "Teusaquillo" :       [0,0,0,0,0,0,0,0,0],
        "Tunjuelito" :        [0,0,0,0,0,0,0,0,0],
        "Bosa" :              [0,0,0,0,0,0,0,0,0],
        "Fuera de Bogotá" :   [0,0,0,0,0,0,0,0,0],
        "Santa Fe" :          [0,0,0,0,0,0,0,0,0],
        "Antonio Nariño" :    [0,0,0,0,0,0,0,0,0],
        "San Cristóbal" :     [0,0,0,0,0,0,0,0,0],
        "Los Mártires" :      [0,0,0,0,0,0,0,0,0],
        "Sin Dato" :          [0,0,0,0,0,0,0,0,0],
    }

    let  total = 0;
    let rangos = []
    let edad = []
    data.map( (per) => {

        if(per["Estado"] === "Fallecido"){
            total = total + 1
            var l = per["Localidad de residencia"];
            var iE = parseInt(per["Edad"]); 
            for(let i=0; i < 9; i++) if (i*10 <= iE && iE <= (i*10 + 9)) loc_edades[l][i]+=1
        }
        return null
    })

    const keys = Object.keys(loc_edades);
    for(let i=0; i < 9; i++){
        let a = 0
        keys.map( (e) =>{ a += loc_edades[e][i]; return null})
        edad.push(a)
    }
    
    // return the ranges for the years
    for(let i=0; i < 9; i++) rangos.push(`${i*10}-${i*10 + 9}`)
    return { edades: edad, rangos: rangos }

}


// add data to bogota
const addDataBog =(data)=> {
    let NumCasos = []
    let Peligrosidad = []
    let Localidades = []
    let totalCasos = 0;
    let AuxPeligrosidad =[]
    
    data.map( (per) => {
        var localidad = per["Localidad de residencia"];
        if(Localidades.includes(localidad)) {
            var idx = Localidades.indexOf(localidad)
            //console.log('idx: ',idx);
            NumCasos[idx] = NumCasos[idx] + 1;
            totalCasos +=1;
        }else {
            Localidades.push(localidad)
            NumCasos.push(1)
            totalCasos+=1;
        }
        return null
    })

    
    for(let i=0; i< NumCasos.length;i++){
        let valorRgb = parseInt((((NumCasos[i]*100)/totalCasos)*255)/100)*3;
        Peligrosidad[i] = valorRgb <=255 ? -valorRgb : 255;
    }

    AuxPeligrosidad[17] = Peligrosidad[0];
    AuxPeligrosidad[16] = Peligrosidad[1];
    AuxPeligrosidad[19] = Peligrosidad[2];
    AuxPeligrosidad[18] = Peligrosidad[3];
    AuxPeligrosidad[10] = Peligrosidad[4];
    AuxPeligrosidad[9] = Peligrosidad[5];
    AuxPeligrosidad[14] = Peligrosidad[6];
    AuxPeligrosidad[4] = Peligrosidad[7];
    AuxPeligrosidad[3] = Peligrosidad[8];
    AuxPeligrosidad[2] = Peligrosidad[9];
    AuxPeligrosidad[6] = Peligrosidad[10];
    AuxPeligrosidad[13] = Peligrosidad[11];
    AuxPeligrosidad[5] = Peligrosidad[12];
    AuxPeligrosidad[1] = Peligrosidad[13];
    AuxPeligrosidad[15] = Peligrosidad[14];
    AuxPeligrosidad[8] = Peligrosidad[15];
    AuxPeligrosidad[11] = Peligrosidad[16];
    AuxPeligrosidad[0] = Peligrosidad[17];
    AuxPeligrosidad[12] = Peligrosidad[18];
    AuxPeligrosidad[7] = Peligrosidad[19];
    
    return {
        numcasosloc: NumCasos,
        localidades: Localidades,
        peligrosidad: AuxPeligrosidad
    };;
}

//add data for popUP in map 
const getCasosPorEstado =(data)=> {
    
    let Localidades = []
    let Estados = []
    
    console.log('addDataPopUp: ', data)

    data.map( (per) => {
        let Estado = {
            "Recuperado": 0,
            "Fallecido": 0,
            "Fallecido ": 0,
            "Grave":0,
            "Moderado":0,
            "Leve":0
        }
        var localidad = per["Localidad de residencia"];
        var estado = per['Estado']
        //console.log('Estado:',estado)
        if(Localidades.includes(localidad)) {
            var idx = Localidades.indexOf(localidad)
            Estados[idx][estado] += 1; 
        }else {
            Estados.push(Estado)
            Localidades.push(localidad)
        }
        return null
    })

    //console.log("Estados: ", Estados)

    return {
        localidades: Localidades,
        Estados: Estados
    };;
}


// get data for polylines
export const getPositions = async () => {
    console.log('getPositions..')
    const response = await fetch(URL_GEO)
        .then(async (response)=>{
            let data = await response.json()
            //console.log('rrr2',data.result.records)
            let dataGeo = orderData(data.features)
            //console.log('numinfun',dataloc)
            return dataGeo
        }).catch(err => console.log(err))
    //console.log('fetcres',response)
    return response
}

// convert array into array of dictionaries
const convert=(data)=>{
    let array = []
    data.map((e) =>{
        let info = {}
        info['lat'] = e[1]
        info['lng'] = e[0]
        array.push(info)
        return null
    })
    return array
}


// order data for return global positions
const orderData =(data)=>{
    let array = []
    data.map( (e) =>{
        let info = {}
        info['0']= e['properties'].LocNombre
        let c = convert(e['geometry'].coordinates['0']['0'])
        info['1']= c
        array.push(info)
        return null
    })
    return array
}
