const defaultState = {
    Polygons: [],
    Age: [],
    State: [],
    Location: [],
    Genre: [],
}

const appReducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'CHANGE_DATA':
            switch(payload[0]){
                case 'Age': return {...state,Age: payload[1]}
                case 'Polygons': return {...state,Polygons: payload[1]}
                case 'Location': {
                    console.log('location-redux')
                    return {...state,Location: payload[1]}
                }
                case 'Genre': return {...state,Genre: payload[1]}
                case 'State': {
                    console.log('State-redux')
                    return {...state,State: payload[1]}   
                }
                default: return{...state,password: payload[1]}
            }
            default:
                return state
    }
}

export default appReducer;