import React  from 'react';
import './loader.css'
 
const Loader = (props) => {
 
 
    return(
        <div style={{justifyContent: 'center', paddingTop: '30px'}} className="row">
            <div  style={{marginLeft: '80px'}} className="col-md-2">
                <div  className="loader"></div>    
            </div>
        </div>
    )
 
}
export default Loader;